.formPage {
    background-size: cover;
    background-position: center;
    background-position: center;
    max-width: 1440px;
    height: 600px;
    padding-top: 50px;
    padding-bottom: 200px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  
  .title {
    font-size: 80px;
    color: #023c02;
    font-weight: 700;
    margin-right: 10vw;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20vw;
  }
  .formText {
    font-size: 40px;
    font-weight: 700;
    line-height: 1.17;
    letter-spacing: 0.05em;
    margin-bottom: 44px;
  }
  
  .label {
    font-size: 25px;
    font-weight: 700;
    line-height: 1.36;
    letter-spacing: 0.1em;
  margin-top: 100px;
    margin-bottom: 40px;
    width: 320px;
    color: #023c02;
  }
  
  .label:focus-within {
    color: rgb(7, 180, 65);
  }
  
  .input {
    width: 300px;
    height: 24px;
    border-top: none;
    border-left: none;
    border-right: none;
    padding-left: 30px;
    background-color: transparent;
    font-family: inherit;
    font-size: 30px;
    border-bottom: 1px solid #023c02;
    color: #023c02;
  }
  .input:focus {
    outline: none;
  }
  
  input:focus-within {
    border-bottom: 1px solid rgb(7, 180, 65);
  }
  
  .button {
    width: 320px;
    height: 80px;
    font-size: 40px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    padding: 0;
    margin-top: 20px;
    box-shadow: 2px 10px 10px rgba(0, 0, 0, 0.85);
  }
  .button a {
    text-decoration: none;
    color: #023c02;
  }
  
  .button:hover,
  .button:focus {
    cursor: pointer;
    color: rgb(7, 180, 65);
  }
  
  .massage {
    color: rgb(7, 180, 65);
    font-size: 40px;
    line-height: 1.17;
    letter-spacing: 0.05em;
    min-height: 100px;
    max-width: 300px;
  }